import React from 'react'
import Img, { FluidObject } from 'gatsby-image'
import { StaticQuery, graphql } from 'gatsby'

interface IFluidImage {
  node: {
    extension: string
    relativePath: string
    childImageSharp: {
      fluid: FluidObject
    }
  }
}

function renderImage(file: IFluidImage) {
  return <Img fluid={file.node.childImageSharp.fluid} />
}

interface IDynamicImageProps {
  path: string
}

const DynamicImage = function({ path }: IDynamicImageProps) {
  return (
    <StaticQuery
      query={graphql`
        query {
          images: allFile(
            filter: { extension: { regex: "/jpeg|jpg|png|gif/" } }
          ) {
            edges {
              node {
                extension
                relativePath
                childImageSharp {
                  fluid(maxWidth: 2048) {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
          }
        }
      `}
      render={({ images }) =>
        renderImage(
          images.edges.find(
            (image: IFluidImage) => image.node.relativePath === path
          ) // returns images like this node: {extension: "png", relativePath: "gatsby-icon.png", childImageSharp: {â€¦}}
        )
      }
    />
  )
}

/**
 * Question remains: how much impact will many (100s) of images have?
 */

export default DynamicImage
