import React, { ReactNode } from 'react'
import styled from 'styled-components'

interface IFixed {
  children: ReactNode
}

const FixedElement = styled.div`
  position: fixed;
  width: 16em;
  height: 16em;
  top: calc(50% - 8em);
  left: calc(50% - 8em);
  z-index: 999;
  background: rebeccapurple;
  border-radius: 999em;
`

export const Fixed = ({ children }: IFixed) => (
  <FixedElement>{children}</FixedElement>
)
